<template>
    <div class="page-container">
        <div class="health">
            <div class="page-header">
                <div class="header-content">
                    <h1>ÂÅ•Â∫∑ËÆ∞ÂΩï</h1>
                    <p>ÂÆàÊä§ÂñµÊòü‰∫∫ÁöÑÂÅ•Â∫∑</p>
                </div>
                <div class="add-buttons">
                    <button class="add-btn" @click="showAddDialog">
                        <i class="el-icon-plus"></i>
                        Ê∑ªÂä†ËÆ∞ÂΩï
                    </button>
                </div>
            </div>

            <div class="pets-filter">
                <div class="pets-list">
                    <div v-for="pet in pets" :key="pet.id" class="pet-item"
                        :class="{ active: selectedPetId === pet.id }" @click="selectPet(pet.id)">
                        <img :src="pet.avatar" :alt="pet.name" class="pet-avatar">
                        <span class="pet-name">{{ pet.name }}</span>
                    </div>
                </div>
            </div>

            <div class="type-filter">
                <div v-for="type in recordTypes" :key="type.value" class="type-tag"
                    :class="{ active: selectedTypes.includes(type.value) }" @click="toggleType(type.value)">
                    <span class="type-icon">{{ type.icon }}</span>
                    <span class="type-label">{{ type.label }}</span>
                </div>
            </div>

            <div class="record-list">
                <div v-for="record in filteredRecords" :key="record.id" class="record-card">
                    <div class="record-date">{{ record.date }}</div>
                    <div class="record-content">
                        <div class="record-header">
                            <div class="pet-info">
                                <img :src="record.petAvatar" :alt="record.petName" class="pet-avatar">
                                <span class="pet-name">{{ record.petName }}</span>
                            </div>
                            <div class="record-type" :class="record.type">
                                <span class="type-icon">{{ getTypeIcon(record.type) }}</span>
                                <span class="type-label">{{ getTypeLabel(record.type) }}</span>
                            </div>
                        </div>
                        <div class="record-body">
                            <h3 class="record-title">{{ record.title }}</h3>
                            <p class="record-description">{{ record.description }}</p>
                            <div v-if="record.nextDate" class="next-date">
                                <span class="icon">üìÖ</span>
                                <span>‰∏ãÊ¨°Êó•ÊúüÔºö{{ record.nextDate }}</span>
                            </div>
                        </div>
                        <div class="record-actions">
                            <button class="action-btn edit" @click="editRecord(record)">
                                <span class="icon">‚úèÔ∏è</span>
                                <span>ÁºñËæë</span>
                            </button>
                            <button class="action-btn delete" @click="deleteRecord(record)">
                                <span class="icon">üóëÔ∏è</span>
                                <span>Âà†Èô§</span>
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <!-- Ê∑ªÂä†/ÁºñËæëËÆ∞ÂΩïÂØπËØùÊ°Ü -->
            <div v-if="dialogVisible" class="dialog-overlay">
                <div class="dialog-content">
                    <div class="dialog-header">
                        <h2>{{ dialogType === 'add' ? 'Ê∑ªÂä†ËÆ∞ÂΩï' : 'ÁºñËæëËÆ∞ÂΩï' }}</h2>
                        <button class="close-btn" @click="dialogVisible = false">√ó</button>
                    </div>
                    <div class="dialog-body">
                        <div class="form-group">
                            <label>ÈÄâÊã©ÂÆ†Áâ©</label>
                            <div class="pets-select">
                                <div v-for="pet in pets" :key="pet.id" class="pet-option"
                                    :class="{ active: recordForm.petId === pet.id }" @click="recordForm.petId = pet.id">
                                    <img :src="pet.avatar" :alt="pet.name" class="pet-avatar">
                                    <span class="pet-name">{{ pet.name }}</span>
                                </div>
                            </div>
                        </div>
                        <div class="form-group">
                            <label>ËÆ∞ÂΩïÁ±ªÂûã</label>
                            <div class="type-select">
                                <div v-for="type in recordTypes" :key="type.value" class="type-option"
                                    :class="{ active: recordForm.type === type.value }"
                                    @click="recordForm.type = type.value">
                                    <span class="type-icon">{{ type.icon }}</span>
                                    <span class="type-label">{{ type.label }}</span>
                                </div>
                            </div>
                        </div>
                        <div class="form-group">
                            <label>Êó•Êúü</label>
                            <input type="date" v-model="recordForm.date" class="form-input">
                        </div>
                        <div class="form-group">
                            <label>Ê†áÈ¢ò</label>
                            <input type="text" v-model="recordForm.title" class="form-input" placeholder="ËØ∑ËæìÂÖ•Ê†áÈ¢ò">
                        </div>
                        <div class="form-group">
                            <label>ÊèèËø∞</label>
                            <textarea v-model="recordForm.description" class="form-textarea"
                                placeholder="ËØ∑ËæìÂÖ•ËØ¶ÁªÜÊèèËø∞"></textarea>
                        </div>
                        <div class="form-group">
                            <label>‰∏ãÊ¨°Êó•Êúü</label>
                            <input type="date" v-model="recordForm.nextDate" class="form-input">
                        </div>
                    </div>
                    <div class="dialog-footer">
                        <button class="cancel-btn" @click="dialogVisible = false">ÂèñÊ∂à</button>
                        <button class="confirm-btn" @click="submitForm">Á°ÆÂÆö</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Health',
    data() {
        return {
            pets: [
                {
                    id: 1,
                    name: 'Â∞èÁÅ∞ÁÅ∞',
                    avatar: require('@/assets/normal_cat.png')
                },
                {
                    id: 2,
                    name: 'Ëä±Ëä±',
                    avatar: require('@/assets/cute_cat.png')
                }
            ],
            recordTypes: [
                { value: 'vaccination', label: 'Áñ´ËãóÊé•Áßç', icon: 'üíâ' },
                { value: 'checkup', label: '‰ΩìÊ£Ä', icon: 'üîç' },
                { value: 'medical', label: 'Â∞±Âåª', icon: 'üè•' }
            ],
            filterForm: {
                petId: 1,
                type: ''
            },
            healthRecords: [
                {
                    id: 1,
                    date: '2024-03-20',
                    petId: 1,
                    petName: 'Â∞èÁÅ∞ÁÅ∞',
                    petAvatar: require('@/assets/normal_cat.png'),
                    type: 'vaccination',
                    title: 'Áå´‰∏âËÅîÁñ´Ëãó',
                    description: 'Êé•Áßç‰∫ÜÁå´‰∏âËÅîÁñ´ËãóÔºåÂåªÁîüÂª∫ËÆÆËßÇÂØü30ÂàÜÈíüÔºåÊó†‰∏çËâØÂèçÂ∫î',
                    nextDate: '2024-04-20'
                },
                {
                    id: 2,
                    date: '2024-03-19',
                    petId: 2,
                    petName: 'Ëä±Ëä±',
                    petAvatar: require('@/assets/cute_cat.png'),
                    type: 'checkup',
                    title: 'Âπ¥Â∫¶‰ΩìÊ£Ä',
                    description: 'ËøõË°å‰∫ÜÂπ¥Â∫¶‰ΩìÊ£ÄÔºåÂêÑÈ°πÊåáÊ†áÊ≠£Â∏∏',
                    nextDate: '2025-03-19'
                }
            ],
            dialogVisible: false,
            dialogType: 'add',
            recordForm: {
                petId: '',
                type: '',
                date: '',
                title: '',
                description: '',
                nextDate: ''
            },
            selectedPetId: null,
            selectedTypes: []
        }
    },
    computed: {
        filteredRecords() {
            let records = [...this.healthRecords]

            if (this.selectedPetId) {
                records = records.filter(record => record.petId === this.selectedPetId)
            }

            if (this.selectedTypes.length > 0) {
                records = records.filter(record => this.selectedTypes.includes(record.type))
            }

            return records.sort((a, b) => new Date(b.date) - new Date(a.date))
        }
    },
    methods: {
        getTypeIcon(type) {
            const typeInfo = this.recordTypes.find(t => t.value === type)
            return typeInfo ? typeInfo.icon : 'üìù'
        },
        getTypeLabel(type) {
            const typeInfo = this.recordTypes.find(t => t.value === type)
            return typeInfo ? typeInfo.label : 'ÂÖ∂‰ªñ'
        },
        selectPet(petId) {
            this.selectedPetId = this.selectedPetId === petId ? null : petId
        },
        toggleType(type) {
            const index = this.selectedTypes.indexOf(type)
            if (index === -1) {
                this.selectedTypes.push(type)
            } else {
                this.selectedTypes.splice(index, 1)
            }
        },
        showAddDialog() {
            this.dialogType = 'add'
            this.recordForm = {
                petId: this.selectedPetId,
                type: '',
                date: new Date().toISOString().split('T')[0],
                title: '',
                description: '',
                nextDate: ''
            }
            this.dialogVisible = true
        },
        editRecord(record) {
            this.dialogType = 'edit'
            this.recordForm = { ...record }
            this.dialogVisible = true
        },
        deleteRecord(record) {
            if (confirm('Á°ÆËÆ§Âà†Èô§ËØ•ËÆ∞ÂΩïÂêóÔºü')) {
                this.healthRecords = this.healthRecords.filter(r => r.id !== record.id)
            }
        },
        submitForm() {
            if (this.dialogType === 'add') {
                const pet = this.pets.find(p => p.id === this.recordForm.petId)
                this.healthRecords.push({
                    id: this.healthRecords.length + 1,
                    petName: pet.name,
                    petAvatar: pet.avatar,
                    ...this.recordForm
                })
            } else {
                const index = this.healthRecords.findIndex(r => r.id === this.recordForm.id)
                const pet = this.pets.find(p => p.id === this.recordForm.petId)
                this.healthRecords[index] = {
                    ...this.recordForm,
                    petName: pet.name,
                    petAvatar: pet.avatar
                }
            }
            this.dialogVisible = false
        }
    }
}
</script>

